import _includes from 'lodash/collection/includes';

class BaseComponent extends React.Component {
  constructor(props) {
    super(props);
  }

  _typeFilter(item) {
    if (!_includes(this.props.condition.type, item.type)) {
      return false;
    }
    return true;
  }

  _familyFilter(item) {
    if (item.family.includes('')) {
      item.family = '';
    }
    if (item.family === '-') {
      item.family = '';
    }

    if (!_includes(this.props.condition.family, item.family)) {
      return false;
    }
    return true;
  }

  _rareFilter(item) {
    if (!_includes(this.props.condition.rare, item.rare)) {
      return false;
    }
    return true;
  }

  _namesFilter(item) {
    if (!_includes(this.props.condition.names, item.name)) {
      return false;
    }
    return true;
  }
}

export default BaseComponent;
